(function() {
  'use strict';

  angular
    .module('uglymona')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
    'use strict';

    angular
        .module('uglymona')
        .controller('MainController', MainController);

    /** @ngInject */
    function MainController($timeout, webDevTec, toastr) {
        var vm = this;

        vm.awesomeThings = [];
        vm.classAnimation = '';
        vm.creationDate = 1458039869479;
        vm.showToastr = showToastr;

        activate();

        function activate() {
            getWebDevTec();
            $timeout(function() {
                vm.classAnimation = 'rubberBand';
            }, 4000);
        }

        function showToastr() {
            toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
            vm.classAnimation = '';
        }

        function getWebDevTec() {
            vm.awesomeThings = webDevTec.getTec();

            angular.forEach(vm.awesomeThings, function(awesomeThing) {
                awesomeThing.rank = Math.random();
            });
        }        
    }
})();

(function() {
  'use strict';

  angular
    .module('uglymona')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
    'use strict';

    angular
        .module('uglymona')
        .config(routerConfig);

    /** @ngInject */
    function routerConfig($stateProvider, $urlRouterProvider) {
        $stateProvider
            .state('landing', {
                url: '/',
                templateUrl: 'app/patial/landing/landing.html',
                controller: 'LandingController',
                controllerAs: 'landing'
            })
            .state('home', {
                url: '/home',
                templateUrl: 'app/main/main.html',
                controller: 'MainController',
                controllerAs: 'main'
            })
            .state('home.beauty', {
                url: '/beauty',
                templateUrl: 'app/patial/beauty/beauty.html',
                controller: 'BeautyController',
                controllerAs: 'beauty'
            })
            .state('home.flip', {
                url: '/flip',
                templateUrl: 'app/patial/flip/flip.html',
                controller: 'FlipController',
                controllerAs: 'flip'
            })
            .state('home.home', {
                url: '/home',
                templateUrl: 'app/patial/home/home.html',
                controller: 'HomeController',
                controllerAs: 'home'
            })        
            .state('home.life', {
                url: '/life',
                templateUrl: 'app/patial/life/life.html',
                controller: 'LifeController',
                controllerAs: 'life'
            })
            .state('home.photograph', {
                url: '/photograph',
                templateUrl: 'app/patial/photograph/photograph.html',
                controller: 'PhotographController',
                controllerAs: 'photo'
            })
            .state('home.romantic', {
                url: '/romantic',
                templateUrl: 'app/patial/romantic/romantic.html',
                controller: 'RomanticController',
                controllerAs: 'rom'
            })
            .state('home.vlog', {
                url: '/vlog',
                templateUrl: 'app/patial/vlog/vlog.html',
                controller: 'VlogController',
                controllerAs: 'vlog'
            });
        $urlRouterProvider.otherwise('/');
    }

})();

